/**
* Media queries
**/
.main-collection-grid {
  padding: 0;
}
.main-collection-grid .product-tabs {
  display: flex;
  justify-content: space-around;
  gap: 20px;
}
@media only screen and (min-width: 992px) {
  .main-collection-grid .product-tabs {
    justify-content: flex-start;
  }
}
.main-collection-grid .product-tabs__title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-1);
  color: var(--clr-smoke-2);
  line-height: 44px;
  letter-spacing: normal;
  text-transform: none;
  opacity: 0.5;
  margin: 0;
}
.main-collection-grid .product-tabs__title--active {
  color: var(--clr-black);
  opacity: 1;
}
@media only screen and (min-width: 400px) {
  .main-collection-grid .product-tabs__title {
    font-size: var(--theme-font-h2-mob-2);
  }
}
@media only screen and (min-width: 992px) {
  .main-collection-grid .product-tabs__title {
    font-size: var(--theme-font-h2-mob-2-1);
    line-height: 44px;
  }
}
.main-collection-grid .product-tabs__separator {
  margin: 0 10px;
  width: 1px;
  background-color: var(--clr-smoke-2);
  opacity: 0.2;
}
.main-collection-grid .product-tabs__separator--vertical {
  height: 40px;
  width: 1px;
}
.main-collection-grid .product-tabs__separator--horizontal {
  height: 1px;
  width: 100%;
  margin: 30px 0 72px;
}
.main-collection-grid .title-category-col {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media only screen and (max-width: 767px) {
  .main-collection-grid .title-category-col {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }
}
.main-collection-grid .sort-by {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 20px;
  font-family: var(--font-family-2-1);
  font-size: var(--theme-font-1);
  color: var(--clr-smoke-2);
  line-height: normal;
}
@media only screen and (max-width: 767px) {
  .main-collection-grid .sort-by {
    width: 100%;
  }
}
.main-collection-grid .sort-by .vert-line {
  width: 2px;
  height: 24px;
  background-color: var(--clr-red);
}
.main-collection-grid .sort-by-select {
  width: 100%;
  min-width: 250px;
  border: none;
}
@media only screen and (max-width: 767px) {
  .main-collection-grid .sort-by-select {
    max-width: none;
  }
}
.main-collection-grid .sort-by-label {
  color: var(--clr-smoke-3);
  width: 100%;
}
.main-collection-grid .product-slider .slick-track {
  display: flex;
  gap: 0 20px;
}
.main-collection-grid .product-slider-wrapper {
  position: relative;
}
.main-collection-grid .product-slider__controller {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 2;
  pointer-events: none;
}
.main-collection-grid .product-slider__controller-button--next,
.main-collection-grid .product-slider__controller-button--prev {
  width: 66px;
  height: 66px;
  aspect-ratio: 1;
  background-color: #d9d9d9;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  position: absolute;
  top: 40%;
  cursor: pointer;
  pointer-events: auto;
}
@media only screen and (max-width: 1759px) {
  .main-collection-grid .product-slider__controller-button--next,
  .main-collection-grid .product-slider__controller-button--prev {
    background-color: var(--clr-black);
    width: 55px;
    height: 55px;
  }
}
@media only screen and (max-width: 1199px) {
  .main-collection-grid .product-slider__controller-button--next,
  .main-collection-grid .product-slider__controller-button--prev {
    top: 30%;
    width: 35px;
    height: 35px;
  }
}
.main-collection-grid .product-slider__controller-button--next svg,
.main-collection-grid .product-slider__controller-button--prev svg {
  max-width: 25px;
}
@media only screen and (max-width: 1759px) {
  .main-collection-grid .product-slider__controller-button--next svg path,
  .main-collection-grid .product-slider__controller-button--prev svg path {
    fill: var(--clr-white);
  }
}
@media only screen and (max-width: 1199px) {
  .main-collection-grid .product-slider__controller-button--next svg,
  .main-collection-grid .product-slider__controller-button--prev svg {
    max-width: 16px;
  }
}
.main-collection-grid .product-slider__controller-button--next {
  left: 100%;
  margin-left: 10px;
}
@media only screen and (max-width: 1759px) {
  .main-collection-grid .product-slider__controller-button--next {
    left: auto;
    right: 0;
    border-radius: 0;
    margin-right: 15px;
  }
}
.main-collection-grid .product-slider__controller-button--prev {
  right: 100%;
  left: auto;
  margin-right: 9px;
}
@media only screen and (max-width: 1759px) {
  .main-collection-grid .product-slider__controller-button--prev {
    right: auto;
    left: 0;
    margin-right: 0;
    border-radius: 0;
    margin-left: 30px;
  }
}
.main-collection-grid .product-slider__controller-button--next:after,
.main-collection-grid .product-slider__controller-button--prev:after {
  display: none;
}
.main-collection-grid .product-slider__controller-button--prev svg {
  transform: rotate(180deg);
}
.main-collection-grid .container-full .product-slider__controller-button--next,
.main-collection-grid .container-full .product-slider__controller-button--prev {
  background-color: var(--clr-black);
  width: 55px;
  height: 55px;
}
.main-collection-grid .container-full .product-slider__controller-button--next svg path,
.main-collection-grid .container-full .product-slider__controller-button--prev svg path {
  fill: var(--clr-white);
}
.main-collection-grid .container-full .product-slider__controller-button--next {
  left: auto;
  right: 0;
  border-radius: 0;
  margin-right: 15px;
}
.main-collection-grid .container-full .product-slider__controller-button--prev {
  right: auto;
  left: 0;
  margin-right: 0;
  border-radius: 0;
  margin-left: 30px;
}
.main-collection-grid .filter-sidebar .horizontal-line {
  margin: 40px 0;
}
.main-collection-grid .filter-sidebar h3 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-btn-tab-1);
  color: var(--clr-smoke-2);
  line-height: 32px;
  letter-spacing: normal;
  margin-bottom: 32px;
}
.main-collection-grid .filter-sidebar .filter-group ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-collection-grid .filter-sidebar .filter-group li {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 20px;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper label {
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-smoke-2);
  line-height: 24px;
  letter-spacing: normal;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper input[type=checkbox] {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  opacity: 0;
  width: 16px;
  height: 16px;
  z-index: 1;
  border: 1px solid var(--clr-smoke-1);
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper .checkmark {
  position: relative;
  border: 1px solid var(--clr-smoke-1);
  background-color: transparent;
  pointer-events: none;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  aspect-ratio: 1/1;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 0;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper .checkmark svg {
  opacity: 0;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper:hover > input[type=checkbox] ~ .checkmark {
  background-color: #ccc;
  border: 1px solid #ccc;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper > input[type=checkbox]:checked ~ .checkmark {
  background-color: var(--clr-red);
  border: 1px solid var(--clr-red);
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper > input[type=checkbox]:checked ~ .checkmark svg {
  opacity: 1;
}
.main-collection-grid .filter-sidebar .filter-group .checkboxWrapper > input[type=checkbox]:checked ~ label {
  font-family: var(--font-family-2-1);
}
.main-collection-grid .filter-sidebar .filter-group__buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
}
.main-collection-grid .filter-sidebar .filter-group__buttons .button.button--outline {
  padding: 5px 30px;
  width: auto;
  border: 2px solid rgba(48, 48, 48, 0.2);
}
.main-collection-grid .filter-sidebar .filter-group__buttons .button.button--outline span {
  color: var(--clr-smoke-2);
}
.main-collection-grid .filter-sidebar .filter-group__buttons .button.button--outline:hover span, .main-collection-grid .filter-sidebar .filter-group__buttons .button.button--outline.active span {
  color: var(--clr-white);
}
.main-collection-grid .filter-sidebar .filter-group__buttons .button.button--outline:after {
  background: var(--clr-smoke-2);
}
.main-collection-grid .filter-sidebar .filter-group__buttons .button.button--outline.active:after {
  transform: translate(0);
}
.main-collection-grid .filter-sidebar .filter-group .range__slider {
  position: relative;
  margin: 40px 0;
  min-height: 2px;
  background-color: rgba(48, 48, 48, 0.2);
}
.main-collection-grid .filter-sidebar .filter-group .range__track {
  position: absolute;
  height: 100%;
}
.main-collection-grid .filter-sidebar .filter-group .range__slide-input {
  background: none;
  position: absolute;
  width: 100%;
  pointer-events: none;
  top: 50%;
  transform: translateY(-50%);
  appearance: none;
}
.main-collection-grid .filter-sidebar .filter-group .range__slide-input[type=range]::-webkit-slider-thumb {
  height: 16px;
  width: 16px;
  border-radius: 50%;
  border: 3px solid var(--clr-red);
  background: var(--clr-red);
  pointer-events: auto;
  appearance: none;
  cursor: pointer;
}
.main-collection-grid .filter-sidebar .filter-group .range__slide-input[type=range]::-moz-range-thumb {
  height: 16px;
  width: 16px;
  border-radius: 50%;
  border: 3px solid var(--clr-red);
  background: var(--clr-red);
  pointer-events: auto;
  appearance: none;
  cursor: pointer;
}
.main-collection-grid .filter-sidebar .filter-group .range__number {
  display: flex;
  text-align: left;
  gap: 11px;
}
.main-collection-grid .filter-sidebar .filter-group .range__number label {
  margin-bottom: 8px;
  display: block;
}
.main-collection-grid .filter-sidebar .filter-group .range__number input {
  padding: 12px 5px 12px 25px;
  width: 100%;
  border: 2px solid rgba(48, 48, 48, 0.2);
  font-family: var(--font-family-2-2);
  font-size: var(--theme-font-btn-mob-1);
  color: var(--clr-black);
  line-height: 24px;
  letter-spacing: normal;
}
.main-collection-grid .filter-sidebar .filter-clear {
  margin-bottom: 32px;
}
.main-collection-grid .filter-sidebar .filter-clear button.clear-btn {
  font-family: var(--font-family-2-2);
  font-size: var(--theme-font-p-mob-1);
  color: var(--clr-smoke-2);
  line-height: 24px;
  letter-spacing: normal;
  text-transform: uppercase;
  background: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  padding-bottom: 5px;
  position: relative;
  cursor: pointer;
}
.main-collection-grid .filter-sidebar .filter-clear button.clear-btn:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: var(--clr-smoke-2);
  transition: all 0.3s ease-in-out;
  bottom: 0;
  left: 0;
}
.main-collection-grid .filter-sidebar .filter-clear button.clear-btn:hover:after, .main-collection-grid .filter-sidebar .filter-clear button.clear-btn.active:after {
  width: 100%;
}
.main-collection-grid .filter-sidebar .advertisement {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 1258px;
}
.main-collection-grid .filter-sidebar .advertisement:after {
  position: absolute;
  content: "";
  background-color: rgba(0, 0, 0, 0.4117647059);
  width: 100%;
  height: 100%;
  z-index: 0;
  top: 0;
}
.main-collection-grid .filter-sidebar .advertisement .img {
  position: absolute;
  width: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.main-collection-grid .filter-sidebar .advertisement .img img {
  height: 100%;
  object-fit: cover;
  object-position: 55%;
}
.main-collection-grid .filter-sidebar .advertisement__text {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 48px;
}
.main-collection-grid .filter-sidebar .advertisement h3 {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-h2-mob-2-1);
  color: var(--clr-white);
  line-height: 44px;
  letter-spacing: normal;
  margin-bottom: 24px;
}